.container {
    width: 1200px;
    margin: 0 auto;

    .swiper {

        height: 100vh;
        .swiper-slide {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .left {
                p{
                    color: #3D3D3D;
                    font-weight: 500;
                    line-height: 16px;
                    letter-spacing: 1.4px;
                    text-transform: uppercase;
                }
                .text__slider{
                color: #727272;
                font-size: 16px;
                font-weight: 400;
                line-height: 24px; 
                font-style: normal;
                display: block;
                margin-bottom: 50px;
                }
                h1 {
                    color: #3D3D3D;
                    font-size: 70px;
                    font-weight: 900;
                    line-height: 70px;
                    text-transform: uppercase;
                    i{
                        color: #46A358;
                        font-style: normal;
                    }
                }
                a{
                    border-radius: 6px;
                    background: #46A358;
                    color: #fff;
                    padding: 10px 26px;
                    margin-top: 20px;
                }
            }
        }
    }

}
.page__content{
    .navigation{
        display: flex;
        ul{
            margin: 0 auto;
            display: flex;
            li:not(:first-child) { 
                margin-left: 50px;
            }
            li {
                color: #3D3D3D;
                font-size: 18px;
                font-weight: 600;
                line-height: 16px;
            }

            li a:hover {
                color: #46A358;
            }
        }
    }

    .box{
        display: flex;
        margin-top: 50px;
        justify-content: space-between;

        .categories {
            background: #FBFBFB;
            padding: 20px;
            width: 310px;

            .categories__title{
                p {
                    color: #3D3D3D;
                    font-weight: 800;
                    font-size: 20px;
                }
            }

            .categories__box{
                margin-top: 20px;
                a{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    margin-top: 20px;
                    color: #3D3D3D;
                    font-weight: 700;

                    i{
                        font-style: normal;
                    }
                }
                a:hover{
                    color: #46A358;

                }
            }

            .categories__box-size{
                margin-top: 50px;
                .categories__box-size__title{
                    color: #3D3D3D;
                    font-weight: 800;
                    font-size: 20px;
                }

                .categories__box-size__box{
                    a{
                        width: 100%;
                    display: flex;
                    justify-content: space-between;
                    margin-top: 20px;
                    color: #3D3D3D;
                    font-weight: 700;
                    }
                    a:hover{
                        color: #46A358;
                    }
                }
            }
            .advertising{
                margin-top: 50px;
                display: flex;
                justify-content: center;
                
            }
        }
    }

    .product{
        .cards{
            display: flex;
            flex-wrap: wrap;
            row-gap: 20px;
            .card {
                background: #ebe9e9;
                padding: 10px;
                margin-left: 20px;

                .cards__title{
                    p {
                        color: #3D3D3D;
                        font-weight: 700;
                        font-size: 16px;
                        margin-top: 10px;
                    }
                }
                .card__prise{
                    color: #46A358;
                    font-weight: 900;
                        font-size: 16px;
                        margin-top: 10px;
                }
            }
        }
    }
}

.more{
    margin-top: 50px;
    .container {
        .cards {
            display: flex;
            justify-content: space-between;
            column-gap: 20px;
            .card{
                display: flex;
                align-items: center;
                background: #ebe9e9;
                padding: 10px;
                img{
                    width: 170px;
                }
                .text{
                    margin-left: 20px;
                    .card__text-tittle{
                        text-transform: uppercase;
                        color: #3D3D3D;
                        font-weight: 900;
                    }
                    .card__text-about{
                        p{
                            color: #3D3D3D;
                            margin-top: 15px;
                            font-weight: 600;
                            margin-bottom: 20px;
                        }
                    }
                    .card__text-button{
                        a{
                            border-radius: 6px;
                    background: #46A358;
                    color: #fff;
                    padding: 10px 26px;
                        }
                    }
                }
            }
        }
    }
}

.blog{
    .container {
        .blog__title{
            margin-top: 50px;
            h1{
            color: #3D3D3D;
            text-align: center;
            font-size: 30px;
            font-weight: 700;
            line-height: 16px;
            }
            p{
            color: #727272;
            text-align: center;
            font-weight: 400;


            }
        }
        .blog__cards{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            column-gap: 20px;
            margin-top: 50px;
            .blog__card{
                background: #ebe9e9;
                padding: 10px;
                margin-top: 20px;
                

                img{
                    width: 250px;
                }
                p{
                    color: #46A358;
                    font-weight: 800;
                    margin-top: 10px;
                }
                h2{
                    font-size: 20px;
                    color: #3D3D3D;
                    margin-top: 10px;
                    font-weight: 700;
                }
                i {
                    color: #727272;
                font-size: 14px;
                font-weight: 400;
                    margin-top: 10px;
                    line-height: 22px;
                }
                a{
                    display: block;
                    color: #3D3D3D;                       
                    font-weight: 500;
                    margin-top: 10px;
                    line-height: 16px;
                }
            }
        }
    }
}