footer {
    margin-top: 50px;
}

.top {
    background: #ebe9e9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0;
    .left {
        flex-basis: 65%;
        margin-right: 20px;
        .cards{
            display: flex;
            // justify-content: space-between;
            .card{
                padding: 10px 30px;
                .card-text{
                    h1{
                        color: #3D3D3D;
                        font-size: 17px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: 16px;
                    }
                    p{
                        color: #727272;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 22px;
                    }
                }
            }
            .card:not(:last-child) {
                border-right: 2px solid rgba(119, 119, 119, 0.582);
            }
        }
    }
    .right{
        flex-basis: 35%;
        .fields-input{
            .fields-input__title {
                h1{
                    color: #3D3D3D;
                    font-size: 22px;
                    font-weight: 700;
                    line-height: 16px;
                }
            }
            .fields-input__input{
                margin-top: 20px;
                input{
                    width: 70%;
                    padding: 10px;
                    border-radius: 5px 0px 0px 5px ;
                }
                a{
                    border-radius: 0px 6px 6px 0px;
                    background: #46A358;
                    padding: 10px 20px;
                    color: #fff;
                }
            }
            .fields-input__text{
                margin-top: 10px;
                color: #727272;
                font-size: 13px;
                font-weight: 400;
                line-height: 22px;
            }
        }
    }
}

.midle{
    display: flex;
    column-gap: 50px;
    background: rgba(70, 163, 88, 0.10);
    padding: 30px 30px;
    border-top: 1px solid rgba(70, 163, 89, 0.514);
    border-bottom: 1px solid rgba(70, 163, 89, 0.514);
    a{
        display: flex;
        align-items: center;
        color: #3D3D3D;
        font-weight: 400;
        line-height: 22px;
    img{
        margin-right: 10px;
        }
    }
}

.bottom{
    display: flex;
    justify-content: space-between;
    .box{
        margin-top: 30px;
        .title {
            margin-bottom: 20px;
            color: #3D3D3D;
            font-size: 18px;
            font-style: normal;
            font-weight: 700;
            line-height: 16px;
        }
        ul {
            li{
                a{
                    color: #3D3D3D;
                    font-weight: 500;
                    line-height: 30px;
                }
            }
        }
        .social{
            ul {
                display: flex;
                column-gap: 20px;
                margin-top: 15px;
                svg{
                    
                }
            }
            .social__title{
                color: #3D3D3D;
                font-size: 18px;
                font-weight: 700;
                line-height: 16px;
            }
            #title {
                color: #3D3D3D;
                font-size: 20px;
                font-weight: 700;
                line-height: 16px;
                margin: 20px 0;
            }
        }
    }
}