header {
    .container {
        padding: 30px 0;
        width: 1200px;
        margin: 0 auto; 
        display: flex;
        justify-content: space-between;
        align-items: center;
        .menu{
            .menu__list {
                display: flex;
                justify-content: space-between;
                .menu__link {
                    color: #3D3D3D;
                    font-size: 18px;
                }
                .menu__item:not(:first-child) {
                    margin-left: 30px;
                }
            }
        }
        .user{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .cart {
                margin-left: 30px;
            }
            .login {
                margin-left: 30px;
                display: flex;
                align-items: center;
                padding: 6px 23px;
                background: #46A358;
                border-radius: 6px;
                a {
                    font-size: 18px;
                    color: #fff;
                }
            }
        }
    }
}